//引入vue
import Vue from "vue"
//引入vuex
import Vuex from 'vuex'
//Vue使用vuex，挂载到vue原型上面
Vue.use(Vuex)

import getters from './getters'//getters
import echart from './modules/echart'//getters



const store = new Vuex.Store({
  modules: {
    echart
  },
  getters
})

export default store