import Vue from 'vue'

// 导入路由
import VueRouter from 'vue-router'

//project模块
import selects from '@/components/project/selects'
import todolist from '@/components/project/todolist'

//Canvans模块
import bubble from "@/components/canvans/bubble"//气泡运动
import qiu from "@/components/canvans/qiu"//砖块撞击
import remove from "@/components/canvans/remove"//元素移动
//Css布局模块
import shengbei from '@/components/Css/shengbei'
import double from '@/components/Css/double'
import reverse from '@/components/Css/reverse'//css翻转
import popup from '@/components/Css/popup'//css翻转
import tab from '@/components/Css/tab'//tab导航

//js
import arry from '@/components/js/arry'//tab导航
import echarts from '@/components/echart/echarts'//tab导航
import gLine from '@/components/g2/line'//tab导航


Vue.use(VueRouter)

const routes = [
  //project
  {
    path: '/',
    component: selects
  },
  {
    path: '/todolist',
    component: todolist
  },
  //canvans
  {
    path: '/bubble',
    component: bubble
  },
  {
    path: '/qiu',
    component: qiu
  },
  {
    path: ' /remove',
    component: remove

  },  //css
  {
    path: '/shengbei',
    component: shengbei
  },
  {
    path: '/double',
    component: double
  },
  {
    path: '/reverse',
    component: reverse
  },
  {
    path: '/popup',
    component: popup
  },
  {
    path: '/tab',
    component: tab
  },
  //js
  {
    path: '/arry',
    component: arry
  },
  {
    path: '/echarts',
    component: echarts
  },
  {
    path: '/gLine',
    component: gLine
  },
]

const router = new VueRouter({
  routes,
  mode: "hash",  //历史模式，没有#，看起来像正常的url地址
  linkActiveClass: 'cur',  // 给高亮的样式自定义一个名字
})

export default router
