<template>
  <div>
    <el-col>
      <el-col style="display:flex">
        <el-col align="left">
          <el-button @click="bar">项目信息-折线图</el-button>
          <el-button @click="lines">项目信息-柱状图</el-button>
        </el-col>
        <el-col align="right">
          <el-button @click="show = !show">使用技术</el-button>
        </el-col>
      </el-col>
      <el-col v-if="tu0">
        <lines :chartData="newMessage" />
      </el-col>
      <el-col v-if="tu1">
        <bar :chartData="newMessage" />
      </el-col>
    </el-col>
    <el-dialog title="提示"
               :visible.sync="show"
               :close-on-press-escape="true"
               width="30%">
      <el-form :model="ruleForm"
               :rules="rules"
               ref="ruleForm"
               label-width="100px"
               class="demo-ruleForm">
        <el-form-item label="技术名称"
                      prop="name">
          <el-input v-model="ruleForm.name"></el-input>
        </el-form-item>
        <el-form-item label="技术程度"
                      prop="lavel">
          <el-select v-model="ruleForm.lavel"
                     style="width:100%"
                     placeholder="请选择活动区域">
            <el-option label="了解"
                       value="25"></el-option>
            <el-option label="熟悉"
                       value="50"></el-option>
            <el-option label="熟练"
                       value="75"></el-option>
            <el-option label="精通"
                       value="100"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary"
                     @click="submitForm('ruleForm')">立即创建</el-button>
          <el-button @click="resetForm('ruleForm')">重置</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>
<script>
import lines from "./line.vue"
import bar from "./bar.vue"
export default {
  components: {
    bar,
    lines
  },
  data() {
    return {
      show: false,
      tu0: true,
      tu1: false,
      ruleForm: {},
      newMessage: [],
      rules: {
        name: [
          { required: true, message: '请输入名称', trigger: 'change' }
        ], lavel: [
          { required: true, message: '请选择程度', trigger: 'change' }
        ],
      }
    }
  },
  mounted() {

  },
  computed: {

  },
  methods: {
    bar() {
      this.tu0 = true
      this.tu1 = false
    },
    lines() {
      this.tu0 = false
      this.tu1 = true
    },
    handleClose() {
    },
    //确定表单
    submitForm() {
      this.newMessage.push(this.ruleForm)
      this.$store.commit('echart/newEch', this.newMessage)
      this.$message.success('添加成功')
      this.show = false
      this.reset()
      console.log(this.ruleForm, 999);
    },
    //取消表单
    resetForm() {
      this.show = false
      this.reset()
    },
    reset() {
      this.ruleForm = {
        name: null,
        lavel: null,
      }
    }
  },
}
</script>
<style lang="scss" scoped>
</style>
