const state = {
  echarts: []
}

const mutations = {
  newEch(state, msg) {
    state.echarts = state.echarts.concat(msg)
    console.log(state.echarts, 77);
    sessionStorage.setItem('echarts', JSON.stringify(state.echarts))
  }
}

const actions = {

}

export default {
  namespaced: true,
  state,
  mutations,
  actions
}