<template>
  <div>
    <h2>数组排序</h2>
    <div>无序列表：{{ messageage }}</div>
    <div ref="message0"></div>
    <div ref="message"></div>
    <div ref="message1"></div>
    <el-button type="primary"
               @click="mao1"
               plain>冒泡排序</el-button>
    <el-button type="primary"
               @click="mao2"
               plain>选择排序</el-button>
    <el-button type="primary"
               @click="mao3"
               plain>快速排序</el-button>
    <el-button type="primary"
               @click="mao4"
               plain>插入排序</el-button>

  </div>
</template>
<script>
export default {
  data() {
    return {
      messageage: [1, 5, 9, 7, 3, 4, 6, 10, 0]
    }
  },
  mounted() {

  },
  computed: {

  },
  methods: {
    //冒泡排序
    mao1() {
      let u1 = this.$refs.message
      let u2 = this.$refs.message0
      let u3 = this.$refs.message1
      u1.innerHTML = ''
      u2.innerHTML = ''
      u3.innerHTML = ''
      let cc = this.messageage
      for (let i = 0; i < cc.length; i++) {
        for (let j = i + 1; j < cc.length; j++) {
          if (cc[i] > cc[j]) {
            let c1 = cc[i]
            cc[i] = cc[j]
            cc[j] = c1
          }
        }
      }
      cc.forEach((item, index) => {
        const li = document.createElement('li')
        li.innerHTML = `<span>${index + 1}:</span>  <span>${item}</span>`
        this.$refs.message.appendChild(li)
      })
      //标题
      const h4 = document.createElement('h4')
      h4.innerHTML = '我是冒泡排序'
      this.$refs.message0.appendChild(h4)
      //原理
      const p = document.createElement('p')
      p.innerHTML = '<div><strong>我是原理：</strong>相邻两个值对比，小的放在前面，大的放在后面。<br/>使用两层for循环，第一层for循环let一个i变量，第二次for循环，let一个i+1变量,在第二层循环里对arr[i]和arr[i+1]进行对比，使用第三个变量存放大的值，小的给arr[i],第三个变量给大的值，这么做事为了让变量不混乱</div>'
      this.$refs.message1.appendChild(p)
    },
    //选择排序
    mao2() {
      let u1 = this.$refs.message
      let u2 = this.$refs.message0
      let u3 = this.$refs.message1
      u1.innerHTML = ''
      u2.innerHTML = ''
      u3.innerHTML = ''
      let cc = this.messageage
      let min, middleValue;//选择排序算法
      for (let i = 0; i < cc.length - 1; i++) {
        min = i
        for (let j = i + 1; j < cc.length; j++) {
          if (cc[j] < cc[min]) {
            min = j
          }
        }
        middleValue = cc[i]
        cc[i] = cc[min]
        cc[min] = middleValue
      }
      cc.forEach((item, index) => {
        const li = document.createElement('li')
        li.innerHTML = `<span>${index + 1}:</span>  <span>${item}</span>`
        this.$refs.message.appendChild(li)
      })

      //标题
      const h4 = document.createElement('h4')
      h4.innerHTML = '我是选择排序'
      this.$refs.message0.appendChild(h4)
      //原理
      const p = document.createElement('p')
      p.innerHTML = '<div><strong>我是原理：</strong>在无序列表n长度的数组里面，抽取第一个和后面n-1的数据作对比，如果有比第一个小的，那他们交换位置，一直到n-1次，遍历剩下的两个数据，找出最小的数据和第一个进行交换，排序完毕</div>'
      this.$refs.message1.appendChild(p)
    },
    //快速排序
    mao3() {
      let u1 = this.$refs.message
      let u2 = this.$refs.message0
      let u3 = this.$refs.message1
      u1.innerHTML = ''
      u2.innerHTML = ''
      u3.innerHTML = ''
      let cc = this.mao3s(this.messageage)
      cc.forEach((item, index) => {
        const li = document.createElement('li')
        li.innerHTML = `<span>${index + 1}:</span>  <span>${item}</span>`
        this.$refs.message.appendChild(li)
      })

      //标题
      const h4 = document.createElement('h4')
      h4.innerHTML = '我是快速排序'
      this.$refs.message0.appendChild(h4)
      //原理
      const p = document.createElement('p')
      p.innerHTML = '<div><strong>我是原理：</strong>在无序列表n长度的数组里面，取中位数，用Math.floor取这个数组长度/2的整数部分，作为中位数，这个中位数作为标准，比他小的在左边，比他大的在右边，<hr/><strong>操作步骤：</strong></br><div>1.let 三个变量，middeleValue中间值，left左边数组，right右边数组</div><div>2.使用for来遍历无序列表，在里面用if判断，里面的每个数据是否大于中间值，大于给left左边数组，小于给右边数组，遍历完后载调用这个方法，让这个方法给left左边数组，right右边数组进行排序，并且合并那个中间值middleValue</div><div></div></div> '
      this.$refs.message1.appendChild(p)

    },
    mao3s(arr) {
      if (arr.length <= 1) {
        return arr
      }
      let middle = arr.splice(Math.floor(arr.length / 2), 1);
      console.log(middle, 99);
      let left = [];
      let right = [];
      for (let i = 0; i < arr.length; i++) {
        if (arr[i] < middle) {
          left.push(arr[i])
        } else {
          right.push(arr[i])
        }
      }
      return this.mao3s(left).concat(middle, this.mao3s(right))

    },
    //插入排序
    mao4() {
      let u1 = this.$refs.message
      let u2 = this.$refs.message0
      let u3 = this.$refs.message1
      u1.innerHTML = ''
      u2.innerHTML = ''
      u3.innerHTML = ''
      let cc = this.messageage
      for (let i = 1; i < cc.length; i++) {
        let middle = cc[i]
        let j = i - 1
        while (cc[j] > middle) {
          cc[j + 1] = cc[j]
          j--
        }
        cc[j + 1] = middle
      }
      cc.forEach((item, index) => {
        const li = document.createElement('li')
        li.innerHTML = `<span>${index + 1}:</span>  <span>${item}</span>`
        this.$refs.message.appendChild(li)
      })

      //标题
      const h4 = document.createElement('h4')
      h4.innerHTML = '我是快速排序'
      this.$refs.message0.appendChild(h4)
      //原理
      const p = document.createElement('p')
      p.innerHTML = '<div><strong>我是原理：</strong></div><div>在数组中，从第一个元素开始，让他和后面的n-1元素进行对比，如果第一个元素大于后面的元素，则他们交换位置，依次进行，排序完成<br></div></div> '
      this.$refs.message1.appendChild(p)
    }

  },
}
</script>
<style scoped>
ul {
  list-style: none;
}
</style>
