<template>
  <div ref="charm"
       :style="{ height: height, width: width }"
       :class="className"></div>
</template>
<script>
import * as echarts from "echarts";

export default {
  props: {
    className: {
      type: String,
      default: "chart",
    },
    width: {
      type: String,
      default: "100%",
    },
    height: {
      type: String,
      default: "430px",
    },
    // 接收父组件传递过来的信息
    chartData: {
      type: Array,
      default: () => [],
    },
    xData: {
      type: Array,
      default: () => [],
    },
    channelDescribe: {
      type: String,
      default: () => "",
    },
    yunit: {
      type: String,
      default: () => "",
    },
    animationDuration: {
      type: Number,
      default: () => 4000,
    },
    type: {
      type: String,
      default: () => "line",
    },
    name1: {
      type: String,
      default: () => "",
    },
  },

  data() {
    return {
      chart: null,
      cc: [],
      value: {
        one: ['html', 'css', 'js', 'es6', 'uniApp', 'echarts', 'g2'],
        two: ['100', '100', '80', '75', '80', '80', '50']
      }
    };
  },
  computed: {
  },
  watch: {
    "value": {
      deep: true,
      handler(val) {
        if (val.one.length == 7) {
          let cc = JSON.parse(sessionStorage.getItem('echarts'))
          cc.forEach((item) => {
            this.value.one.push(item['name'])
            this.value.two.push(item['lavel'])
          })
          console.log(val, cc, 515);
        }
      },
      immediate: true
    },
    chartData: {
      deep: true,
      handler(val) {
        console.log(val, this.$store.state.echart.echarts, 999);
        if (val.length > 1) {
          this.value.one.push(val[val.length - 1].name)
          this.value.two.push(val[val.length - 1].lavel)
        } else if (val.length == 1) {
          this.value.one.push(val[0].name)
          this.value.two.push(val[0].lavel)
        } else {
          // let cc = sessionStorage.getItem('echarts')
          // for (let i of this.value) {
          // console.log(this.value, 11);
          // }
          // this.value.forEach((item, index) => {
          //   cc.forEach((ite, inde) => {
          //     // item.
          //     console.log(item, index, ite, inde, 333);
          //   })
          // })
        }
        this.init();
      },
      // immediate: true
    },
  },
  mounted() {
    this.init()
  },
  created() {
  },
  methods: {
    init() {
      let that = this
      that.chart = echarts.init(this.$refs.charm)
      that.chart.clear()
      that.chart.setOption({
        title: {
          text: 'App活跃量'
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'shadow'
          }
        },
        legend: {
          data: [...this.value.one]
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        toolbox: {
          feature: {
            saveAsImage: {}
          }
        },
        xAxis: {
          type: 'category',
          boundaryGap: false,
          data: this.value.one
        },
        yAxis: {
          type: 'value'
        },
        series: [
          {
            // name: 'skill',
            type: 'line',
            stack: 'Total',
            data: [...this.value['two']]
          }
        ]
      }, true)
      window.onresize = function () {
        that.chart.resize();
      }
    }
  },
};
</script>
<style scoped>
